@import url(https://fonts.googleapis.com/css2?family=Quicksand:wght@300;500;700&display=swap);
/* base styles */

* {
    margin: 0;
    font-family: 'Quicksand';
    color: #333;
}

.navbar {
    padding: 20px;
    display: flex;
    align-items: center;
    max-width: 600px;
    margin: 0 auto;
    border-bottom: 1px solid #f2f2f2;
}

.navbar h1 {
    color: #f1356d;
}

.navbar .links {
    margin-left: auto;
}

.navbar a {
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.navbar a:hover {
    color: #f1356d;
}

.content {
    max-width: 600px;
    margin: 40px auto;
    padding: 20px;
}


/* blog previews*/

.blog-list input[type='text'] {
    width: 50%;
    padding: 12px 20px;
    margin: 8px 0;
    box-sizing: border-box;
    border: none;
    border-bottom: 2px solid #f1356d;
}

.blog-list input[type='text']:focus {
    outline: none;
}

.blog-preview {
    padding: 10px 16px;
    margin: 20px 0;
    border-bottom: 1px solid #fafafa;
}

.blog-preview:hover {
    box-shadow: 1px 3px 5px rgba(0, 0, 0, 0.1);
}

.read-more :hover {
    color: #000;
    background-color: #f1356d;
}

.blog-preview h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 8px;
}

.blog-preview a {
    text-decoration: none;
}

.blog-preview span a:hover {
    background-color: #ddd;
    padding: 0.5%;
    border-radius: 8px;
}


/* blog details page */

.blog-details h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 10px;
}

.blog-details div {
    margin: 20px 0;
}

.blog-details button {
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}


/* create new blog form */

.create {
    max-width: 400px;
    margin: 0 auto;
    text-align: center;
}

.create label {
    text-align: left;
    display: block;
}

.create h2 {
    font-size: 20px;
    color: #f1356d;
    margin-bottom: 30px;
}

.create input,
.create textarea,
.create select {
    width: 100%;
    padding: 6px 10px;
    margin: 10px 0;
    border: 1px solid #ddd;
    box-sizing: border-box;
    display: block;
}

.create button {
    background: #f1356d;
    color: #fff;
    border: 0;
    padding: 8px;
    border-radius: 8px;
    cursor: pointer;
}


/* Footer  */

.footer {
    padding: 20px;
    max-width: 600px;
    margin: 0 auto;
    border-top: 1px solid #f2f2f2;
    display: grid;
    grid-template-columns: 1fr 1fr;
}

.footer a {
    display: block;
    margin-left: 16px;
    text-decoration: none;
    padding: 6px;
}

.footer a:hover {
    color: #f1356d;
}

.footer p {
    /* float: right; */
}
